
/**
 * 消息中心
 * 功能：  监听 转发 信息
 * author：lidongdong
 * time:2018.9.17
 * 1在使用过程中，需要将该脚本引用到相应的JS文件中去，然后，通过在方法来设置事件监听，
 * 2通过EMIT来发送消息.popAll方法用来清空监听列表，emitAll用来派发之前没有发送出来的消息。
    在项目的脚本或者组件初始化的过程中，先调用的方法，设置所有的事件监听。然后emitAll，
    保证在监听设置的过程中，收到的网络消息不被丢弃。在emitAll方法中，会对消息进行判断，
    如果当前事件监听列表中没有事件监听，就先将收到的消息保存，然后等初始化完成并设置好事件监听后，
    统一调用emitAll方法，将所有的消息一起发出来。保证不丢包。
   3在当前场景的根节点的销毁方法中，调用popAll方法，保证在当前场景销毁的时候，所有的监听被移除掉了。
 */
window.wsft = window.wsft || {}

/**
 * bindFuncList结构如下
 * [
 * 'event1':[func1,func2],
 * 'event2',[func3,func4]
 * ]
 */
var bindFuncList = [];// 保存监听函数
/**
 * 当暂时没有监听，或者场景没有初始化的时候，会将提前收到的消息和数据，
 * 保存到emitList中，结构如下：
 * [
 * 'event1':[args1,args2],
 * 'event2':[args3,args4];
 * ]
 */
var emitList = [];

var message = {
/**
 * 设置监听
 * @param {监听的事件的名字} key 
 * @param {监听的回调方法} cbFunc 
 */
// 设置事件监听
    on:function(key,cbFunc){
        if (bindFuncList[key]){
            bindFuncList[key].push(cbFunc);
        }else {
            var ary = new Array();
            ary.push(cbFunc);
            bindFuncList[key] = ary;
        }
    },
/**
 * 触发事件监听函数
 * @param {监听的事件的名字} key 
 * @param {调用时传的参数} args 
 */
// emit事件，发送消息
    emit:function(key,args){
        var ary = bindFuncList[key];
        if(ary){// 如果已经注册了事件，就直接发送消息
            for (var i in ary) {
                if (ary.hasOwnProperty(i)) {
                    try {
                        ary[i].call(this,args);
                    } catch (error) {
                        
                    }
                }
            }
        }else {// 没有注册，先将要发送的消息保存，然后等待事件注册后，再一起emit
            if (emitList[key]){
                emitList[key].push(args);
            }else {
                var ary = new Array();
                ary.push(args);
                emitList[key] = ary;
            }
        }
    },
// emitAll，将所有消息都emit
    emitAll:function(){
        for (var key in emitList) {
            if (emitList.hasOwnProperty(key)) {
                var emitAry = emitList[key];
                for (var j in emitAry) {
                    if (emitAry.hasOwnProperty(j)) {
                        var args = emitAry[j];// 去除参数
                        var ary = bindFuncList[key];// 去除监听的方法
                        // 开始执行事件
                        for (var iterator in ary) {
                            if (ary.hasOwnProperty(iterator)) {
                                try {
                                    ary[iterator].call(this,args);
                                } catch (error) {

                                }
                            }
                        }
                        
                    }
                }
            }
        }
        emitList = [];
    },

// 清空全部的事件监听
    popAll:function(){
        bindFuncList = [];
    }

}

wsft.message = module.exports = message;

