
cc.Class({
    extends: cc.Component,

    properties: {

        musicVolume:0.5,    
        soundVolume:0.5,
        bgMusicAudioID:-1, //当前背景音乐的ID
    }, 
    ctor:function() {
        var m = cc.sys.localStorage.getItem("musicVolume");
        if(m != null){
            this.musicVolume = parseFloat(m);//将string转化为float
        }
        var s = cc.sys.localStorage.getItem("soundVolume");
        if(s != null){
            this.soundVolume = parseFloat(s);    
        }

        // cc.game.on(cc.game.EVENT_HIDE, function () {
        //     console.log("cc.audioEngine.pauseAll");
        //     cc.audioEngine.pauseAll();
        // });
        // cc.game.on(cc.game.EVENT_SHOW, function () {
        //     console.log("cc.audioEngine.resumeAll");
        //     cc.audioEngine.resumeAll();
        // });
    },

    getUrl:function(url){
        return cc.url.raw("resources/sounds/" + url);
    },
    //播放音乐
    playMusic(url){
        var audioUrl = this.getUrl(url);
        console.log(audioUrl);
        if(this.bgMusicAudioID >= 0){
            cc.audioEngine.stop(this.bgMusicAudioID);
        }
        this.bgMusicAudioID = cc.audioEngine.play(audioUrl,true,this.musicVolume);
    },
    //播放音效
    playSound(url){
        var audioUrl = this.getUrl(url);
        if(this.soundVolume > 0){
            var audioId = cc.audioEngine.play(audioUrl,false,this.soundVolume);    
        }
    },
    //设置音效大小
    setSoundVolume:function(v){
        if(this.soundVolume != v){
            cc.sys.localStorage.setItem("soundVolume",v);
            this.soundVolume = v;
        }
    },
    //设置音乐大小
    setMusicVolume:function(v){
        if(this.bgMusicAudioID >= 0){
            if(v > 0){
                cc.audioEngine.resume(this.bgMusicAudioID);//恢复播放指定的音频
            }
            else{
                cc.audioEngine.pause(this.bgMusicAudioID);//暂停正在播放音频。
            }
            //cc.audioEngine.setVolume(this.bgmAudioID,this.bgmVolume);
        }
        if(this.musicVolume != v){
            cc.sys.localStorage.setItem("musicVolume",v);
            this.musicVolume = v;
            cc.audioEngine.setVolume(this.bgMusicAudioID,v);//设置音量（0.0 ~ 1.0）
        }
    },
    pauseAll:function(){
        cc.audioEngine.pauseAll();//暂停现在正在播放的所有音频。
    },
    resumeAll:function(){
        cc.audioEngine.resumeAll();//恢复播放所有之前暂停的所有音频。
    }
});
