
/**
 * 事件封装回调
 * 功能：  对于btn 等一些事件进行的封装
 * author：lidongdong
 * time:2018.8.30
 */
window.wsft = window.wsft || {}
var event = {
    /*
        添加按钮事件监听
        1 btn 这个btn的node
        2 node 这个 node 节点是btn事件处理代码组件所属的节点
        3 MyComponent 这个是btn所属代码文件名 string类型
        4 callback btn的回调函数 string类型
        5 customEventData 自定义事件数据
    */
   addBtnEvent:function(btn,node,MyComponent,callback,customEventData){
        var clickEventHandler = new cc.Component.EventHandler();
        clickEventHandler.target = node; 
        clickEventHandler.component = MyComponent;
        clickEventHandler.handler = callback;
        clickEventHandler.customEventData = customEventData;

        var button = btn.getComponent(cc.Button);
        button.clickEvents.push(clickEventHandler);
    },
    /**
     * 添加滑动事件监听（slider）
     * _slider 这个slider cc.find得到的（node）
     * _node 这个 node 节点是你的事件处理代码组件所属的节点
     * _MyComponent 这个是slider所属代码文件名 string类型
     * _callback  slider回调函数 string类型
     * _customEventData 自定义事件数据
     */
    addSliderEvent:function(_slider,_node,_MyComponent,_callback,_customEventData){
        var sliderEventHandler = new cc.Component.EventHandler();
        sliderEventHandler.target = _node; //
        sliderEventHandler.component = _MyComponent;
        sliderEventHandler.handler = _callback;
        sliderEventHandler.customEventData = _customEventData;

        var slider = _slider.getComponent(cc.Slider)
        slider.slideEvents.push(sliderEventHandler);
    },
    /**
     * CheckBox复选框
     * _checkBox 这个_checkBox cc.find得到的（node）
     * _node 这个 node 节点是你的事件处理代码组件所属的节点
     * _MyComponent 这个是_checkBox所属代码文件名 string类型
     * _callback  _checkBox回调函数 string类型
     * _customEventData 自定义事件数据 
     */
    addCheckBoxEvent:function(_checkBox,_node,_MyComponent,_callback,_customEventData){
        var checkEventHandler = new cc.Component.EventHandler();
        checkEventHandler.target = _node; //这个 node 节点是你的事件处理代码组件所属的节点
        checkEventHandler.component = _MyComponent;
        checkEventHandler.handler = _callback;
        checkEventHandler.customEventData = _customEventData;

        var toggle = _checkBox.getComponent(cc.Toggle)
        toggle.checkEvents.push(checkEventHandler);
    },
}
wsft.event = module.exports = event;
