var _copy = require("copy_h5")
cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var self = this
        this.initAllBtn()
        var info_bg = cc.find('Canvas/info_layer/info_bg');
        var callback = function(){
            self.node.active = false;
        }
        wsft.uiFuncs.bindTouchLayer(this.node,info_bg,callback);
        this.refreshInfo()
    },

    start () {

    },
    refreshInfo:function(){
        var _name = cc.find('Canvas/info_layer/info_name').getComponent(cc.Label);
        var _id= cc.find('Canvas/info_layer/info_id').getComponent(cc.Label);
        var _ip= cc.find('Canvas/info_layer/info_ip').getComponent(cc.Label);
        _name.string = wsft.ME.userName
        _id.string = wsft.ME.userId
        _ip.string = wsft.ME.ip
    },
    allBtnCallback:function(event,name){
        cc.log(name)
        if(name === "close"){
            this.node.active = false
        }else if(name === "copy"){
            var _copyStr = ""+wsft.ME.userId
            if(cc.sys.isBrowser){//判断是否是浏览器
                if(_copy(_copyStr)){
                    wsft.uiFuncs.showTip("复制ID成功",32)
                }else{
                    wsft.uiFuncs.showTip("复制ID失败",32)
                }
            }else{

            }
        }

    },
   // 初始化所有的btn
   initAllBtn:function(){
        var self = this
        var btnHader = function(btnPath,name){
            var btn = cc.find(btnPath)
            wsft.event.addBtnEvent(btn,self.node,'infoLayer','allBtnCallback',name);
        }
        btnHader('Canvas/info_layer/close_btn','close'); //close
        btnHader('Canvas/info_layer/copy_btn','copy'); //copy
       
   },
    // update (dt) {},
});
