window.wsft = window.wsft || {}
var uiFuncs = {
    /*
        showTip显示tip
        _txt 显示的内容
        _fontSize 字体的大小 默认 40 
        _time 显示的时间 默认为 2 
        _color 字体的颜色 new cc.Color(255, 255, 73
     */
    showTip:function (_txt,_fontSize,_time,_color) {
        cc.loader.loadRes("common/prefabs/showTip/showTip", function (err, prefab) {
            var showTime = _time || 2
            var fontSize = _fontSize || 40
            var color = _color || (new cc.Color(0, 0, 0))
            var tipNode = cc.instantiate(prefab);
            var _tipBg = tipNode.getChildByName("showTip_bg")
            var _label = tipNode.getChildByName("tipLabel").getComponent(cc.Label);
            // var _label = tipNode.getComponent(cc.Label)
            var scene = cc.director.getScene()
            scene.addChild(tipNode);
            var tipNodeX = cc.winSize.width * .5
            var tipNodeY = cc.winSize.height * .5 
            tipNode.x = tipNodeX
            tipNode.y = tipNodeY - 100
            tipNode.color =  color
            _label.fontSize = fontSize
            _label.fontHeight = fontSize
            _label.string = _txt;
            _label._updateRenderData(true);// 
            // console.log(_label.node.width)
            _tipBg.width = _label.node.width + 20
            _tipBg.height =  fontSize + 10
            var callFunc = function(){
                tipNode.destroy();
            }
            var action1 = cc.moveTo(.5, cc.v2(tipNodeX, tipNodeY));
            var action2 = cc.delayTime(showTime)
            var action3 =  cc.callFunc(callFunc)
            var seq = cc.sequence(action1,action2,action3);
            tipNode.runAction(seq);
        });
    },
     /*
        * 弹窗添加触摸处理层
        * _touchNode: 弹窗跟节点，用于添加触摸，必须全屏
         * _node: 传入一个节点，用于判断区域
        * _callback: 点击在弹窗外时的处理
    */
    bindTouchLayer: function (_touchNode, _node, _callback) {
        // 判断是否在_node外面
        _touchNode.on(cc.Node.EventType.TOUCH_END, function (_event) {
            var pos = _event.getLocation();
            // 在_node节点外面
            if (!_node.getBoundingBoxToWorld().contains( _event.getLocation())) {
                var callback = _callback || function () { };
                callback();
            }
        });
    },

      /**
         * 控制姓名长度
         * @param name         字符串
         * @param Ilength      最大长度控制
         * @param insertStr    补字符串
         * by xiewei welfu@vip.qq.com welfu@vip.qq.com
         */
        trimName:function(name, Ilength, insertStr)
        {
            if(!name)
            {
                return 'NULL';
            }
            name = name.toString();

            var tmp=0; //中文字符数记录
            var len=0; //单字符处理
            var okLen=0; //实际字符串长度
            var dowble = 0; //4字节字符

            Ilength *=2;//转为单字符长度，便于中英混杂。

            for(var i=0;i<name.length;i++){

                if(name.charCodeAt(i)>255){
                    tmp+=2;
                }else{
                    len+=1;
                }
                    
                okLen+=1;

                //解决中英文混杂长度控制
                if(tmp+len>Ilength)
                {
                    okLen += dowble - 1;
                    return (name.substring(0,okLen)+insertStr);
                }
                

                if(name.codePointAt(i) > 0xFFFF){
                    i++;
                
                    if(i < name.length){
                        dowble += 1;
                    }
                }
            }

            return (name.substring(0, okLen+dowble));
        },
}
wsft.uiFuncs = module.exports = uiFuncs;
