cc.Class({
    extends: cc.Component,

    properties: {
       
    
   
    },

    // LIFE-CYCLE CALLBACKS:
    onLoad () {
        this.initAllBtn()
        this.join_room_layer = cc.find("Canvas/join_room_layer")
        this.rule_layer = cc.find("Canvas/rule_layer")
        this.create_room_layer = cc.find("Canvas/create_room_layer")
        this.shop_layer = cc.find('Canvas/shop_layer')
        this.news_layer = cc.find('Canvas/news_layer')
        this.set_layer = cc.find('Canvas/set_layer')
        this.infoLayer = cc.find('Canvas/info_layer')
        this.recordLayer = cc.find('Canvas/record_layer')
        this.shareLayer = cc.find('Canvas/share_layer')

        
        // wsft.message.emitAll()
    },
    
    start () {
        this.notice()
        this.refreshInfo()
    }, 

    refreshInfo:function(){
        var _name = cc.find('Canvas/top_node/info_node/name_lable').getComponent(cc.Label);
        var _id = cc.find('Canvas/top_node/info_node/id_lable').getComponent(cc.Label);
        var _diamond= cc.find('Canvas/top_node/info_node/diamond_bg/diamond_num').getComponent(cc.Label);
        _name.string = wsft.uiFuncs.trimName(wsft.ME.userName,4,"...")
        _id.string = wsft.ME.userId
        _diamond.string = wsft.ME.diamond
    },
    //公告
    notice(){
        var noticeNode = cc.find('Canvas/notice_node')
        this.noticeJS = noticeNode.getComponent('notice')
        //添加默认公告（一种循环这个公告）
        this.noticeJS.setDefaultNotice("北京网圣飞腾欢迎你！北京网圣飞腾欢迎你！北京网圣飞腾欢迎你！")  
        //添加广播  就播放一次
        this.noticeJS.addNoticeTable("你好！你好！")
    },
    allBtnCallback:function(event,name){
        cc.log(name)
        var self = this
        if(name === "creatroom"){
            this.create_room_layer.active = true
        }
        else if(name === "joinroom"){
            this.join_room_layer.active = true
        }
        else if(name === "gold" || name == "diamond"){
           this.shop_layer.active = true
        }
        else if(name === "rule"){
            this.rule_layer.active = true
        }
        else if(name === "set"){
            this.set_layer.active = true
        }
        else if(name === "record"){
            this.recordLayer.active = true
        }
        else if(name === "share"){
            this.shareLayer.active = true
        }else if(name === "news"){
            this.news_layer.active = true
        }else if(name === "myBSS"){
            wsft.uiFuncs.showTip('敬请期待！',40)
        }else if(name === "head"){
           this.infoLayer.active = true
        }else{

        }

    },
   // 初始化所有的btn
   initAllBtn:function(){
       cc.log("btn===init")
       var self = this
       var btnHader = function(btnPath,name){
          var btn = cc.find(btnPath)
          wsft.event.addBtnEvent(btn,self.node,'mainScene','allBtnCallback',name);
       }
       btnHader('Canvas/center_node/createRoom_btn','creatroom'); //创建房间
       btnHader('Canvas/center_node/joinRoom_btn','joinroom'); //加入房间
       btnHader('Canvas/center_node/myBSS_btn','myBSS'); //我的社区
       btnHader('Canvas/top_node/btn_node/news_btn','news'); //信息
       btnHader('Canvas/top_node/btn_node/rule_btn','rule'); //玩法
       btnHader('Canvas/top_node/btn_node/set_btn','set'); //设置
       btnHader('Canvas/top_node/btn_node/record_btn','record'); //战绩
       btnHader('Canvas/top_node/btn_node/share_btn','share'); //分享 

       btnHader('Canvas/top_node/info_node/head_mask','head'); //头像）
       btnHader('Canvas/top_node/info_node/diamond_bg/add_diamond_btn','diamond'); //添加砖石（房卡）
       btnHader('Canvas/top_node/info_node/gold_bg/add_gold_btn','gold'); //添加金币
       
   },
    // update (dt) {},
});
