

var MODEL = [
                [   
                    ["6局(砖石×4)","12局(砖石×4)","26局(砖石×6)"], //f 2
                    ["5局(砖石×4)","10局(砖石×4)","20局(砖石×6)"], //f 3
                    ["1圈(砖石×4)","2圈(砖石×4)","4圈(砖石×6)"], //f 4]
                ],
                [   
                    ["6局(砖石×2/人)","12局(砖石×2/人)","26局(砖石×3/人)"], //aa 2
                    ["5局(砖石×2/人)","10局(砖石×2/人)","20局(砖石×2/人)"], //aa 3
                    ["1圈(砖石×1/人)","2圈(砖石×1/人)","4圈(砖石×2/人)"], //aa 4
                ]
            ]
cc.Class({
    extends: cc.Component,
    properties: {
        checkedColor:new cc.Color(255, 0, 0),//选中的颜色
        noCheckedColor:new cc.Color(150, 115, 110),//未选中的颜色
        createLabel:[], 
        createToggle:[],
    },
    onLoad(){
        this.initcheckBox()
        for(var i = 1;i<4;++i){
            this.createLabel.push(cc.find("Canvas/create_room_layer/create_rule_node_1/model_node/toggle"+i+"/label"))
            this.createToggle.push(cc.find("Canvas/create_room_layer/create_rule_node_1/model_node/toggle"+i))
        }
        for(var i = 1;i<3;++i){
            this.createLabel.push(cc.find("Canvas/create_room_layer/create_rule_node_1/pay_node/toggle"+i+"/label"))
            this.createToggle.push(cc.find("Canvas/create_room_layer/create_rule_node_1/pay_node/toggle"+i))
        }
        for(var i = 1;i<4;++i){
            this.createLabel.push(cc.find("Canvas/create_room_layer/create_rule_node_1/player_num_node/toggle"+i+"/label"))
            this.createToggle.push(cc.find("Canvas/create_room_layer/create_rule_node_1/player_num_node/toggle"+i))
        }
        
        for(var i = 1;i<7;++i){
            this.createLabel.push(cc.find("Canvas/create_room_layer/create_rule_node_1/rule_node/toggle_node_"+i+"/label"))
            this.createToggle.push(cc.find("Canvas/create_room_layer/create_rule_node_1/rule_node/toggle_node_"+i))
        }
    },
    initColor:function(){
        for(var i = 0;i<this.createLabel.length;++i){
            if(this.createToggle[i].getComponent(cc.Toggle).isChecked){ //cc.Color(255, 255, 73)
                this.createLabel[i].color = this.checkedColor
            }else{
                this.createLabel[i].color = this.noCheckedColor
            }
        }
    },
    setLabelColor:function(_colorTable){
        for(var i = 0;i<this.createLabel.length;++i){
            this.createLabel[i].color = this.noCheckedColor
            this.createToggle[i].getComponent(cc.Toggle).isChecked = false
        }
        for(var i = 0;i<_colorTable.length;++i){
            this.createLabel[_colorTable[i]].color = this.checkedColor
            this.createToggle[_colorTable[i]].getComponent(cc.Toggle).isChecked = true

            console.log(_colorTable[i])
        }
    },
    modelToggle:function(_num){
        for(var i = 0;i<3;++i){
            if(_num == i){
                this.createLabel[i].color = this.checkedColor
            }else{
                this.createLabel[i].color = this.noCheckedColor
            }
        }
    },
    payToggle:function(_num){
        for(var i = 3;i<5;++i){
            if(_num == i){
                this.createLabel[i].color = this.checkedColor
            }else{
                this.createLabel[i].color = this.noCheckedColor
            }
        }
    },
    playerToggle:function(_num){
        for(var i = 5;i<8;++i){
            if(_num == i){
                this.createLabel[i].color = this.checkedColor
            }else{
                this.createLabel[i].color = this.noCheckedColor
            }
        }
    },
    ruleToggle:function(_num){
        if(this.createToggle[_num].getComponent(cc.Toggle).isChecked){ //cc.Color(255, 255, 73)
            this.createLabel[_num].color = this.checkedColor
        }else{
            this.createLabel[_num].color = this.noCheckedColor
        }
    },

    //判断谁陪选中
    judgeChecked:function(){
        var judgeTable = []
        for(var i = 0;i<this.createLabel.length;++i){
            if(this.createToggle[i].getComponent(cc.Toggle).isChecked){ //cc.Color(255, 255, 73)
                judgeTable.push(i)
                // this.createLabel[i].color = this.checkedColor
            }else{
                // this.createLabel[i].color = this.noCheckedColor
            }
        }
        return judgeTable;
    },

    initToggleLabel:function(_pay,_player){
        var _playerNum = 0
        var _payNum = 0
        if(_player){
            _playerNum = _player
        }else{
            for(var i = 5;i < 8;++i){
                if(this.createToggle[i].getComponent(cc.Toggle).isChecked){ 
                    _playerNum = i
                }
            }
        }
        if(_pay){
            _payNum = _pay
        }else{
            for(var i = 3;i < 5;++i){
                if(this.createToggle[i].getComponent(cc.Toggle).isChecked){ 
                    _payNum = i
                }
            }
        }
        for(var i = 0;i < 3;++i){
            this.createLabel[i].getComponent(cc.Label).string = MODEL[_payNum - 3][_playerNum-5][i]
            // console.log(MODEL_F[_num-5][i])
        }
    },
    //_Toggle
    checkBoxCallback:function(toggle, name){
        // console.log(toggle)
        console.log(name)
        // if(name == "toggle"){
        //     if(toggle.isChecked){ //cc.Color(255, 255, 73)
        //         wsft.uiFuncs.showTip('暂时未开通GPS功能！',30,2,(new cc.Color(0, 0, 0)))
        //     }else{
        //         wsft.uiFuncs.showTip('暂时未开通GPS功能',30,2,(new cc.Color(0, 0, 0)))
        //     }
        // }
        if(name == "model_1"){
            this.modelToggle(0)
        }else if(name == "model_2"){
            this.modelToggle(1)
        }else if(name == "model_3"){
            this.modelToggle(2)
        }else if(name == "pay_1"){
            this.payToggle(3)
            this.initToggleLabel(3,null)
        }else if(name == "pay_2"){
            this.payToggle(4)
            this.initToggleLabel(4,null)
        }else if(name == "player_1"){
            this.playerToggle(5)
            this.initToggleLabel(null,5)
        }else if(name == "player_2"){
            this.playerToggle(6)
            this.initToggleLabel(null,6)
        }else if(name == "player_3"){
            this.playerToggle(7)
            this.initToggleLabel(null,7)
        }else if(name == "rule_1"){
            this.ruleToggle(8)
        }else if(name == "rule_2"){
            this.ruleToggle(9)
        }else if(name == "rule_3"){
            this.ruleToggle(10)
        }else if(name == "rule_4"){
            this.ruleToggle(11)
        }else if(name == "rule_5"){
            this.ruleToggle(12)
        }else if(name == "rule_6"){
            this.ruleToggle(13)
        }
    },
    initcheckBox:function(){
        console.log("434343")
        var self = this 
        var checkBoxhander = function(_sliderPath,_name){
            var slider = cc.find(_sliderPath)
            wsft.event.addCheckBoxEvent(slider,self.node,'ChiFengMJ','checkBoxCallback',_name)
        }
        for(var i = 1;i<4;++i){
            checkBoxhander("Canvas/create_room_layer/create_rule_node_1/model_node/toggle"+i,"model_"+i)
            checkBoxhander("Canvas/create_room_layer/create_rule_node_1/player_num_node/toggle"+i,"player_"+i)
        }
        for(var i = 1;i<7;++i){
            checkBoxhander("Canvas/create_room_layer/create_rule_node_1/rule_node/toggle_node_"+i,"rule_"+i)
        }
        checkBoxhander("Canvas/create_room_layer/create_rule_node_1/pay_node/toggle1","pay_1")
        checkBoxhander("Canvas/create_room_layer/create_rule_node_1/pay_node/toggle2","pay_2")
        
    },
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    start () {

    },

    // update (dt) {},
});
