cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var self = this
        this.initAllBtn()
        this.initSlider()
        this.initcheckBox()
        var set_bg = cc.find('Canvas/set_layer/set_bg');
        var callback = function(){
            self.node.active = false;
        }
        wsft.uiFuncs.bindTouchLayer(this.node,set_bg,callback);
        this.refreshVolume()

    },

    start () {
    },
    //active = true
    onEnable(){
        this.refreshVolume()
    },
    refreshVolume:function(){
        var _musicMask = cc.find('Canvas/set_layer/set_node/slider_music/mask_node')
        var _soundMask = cc.find('Canvas/set_layer/set_node/slider_sound/mask_node')
        var sliderMusic = cc.find('Canvas/set_layer/set_node/slider_music').getComponent(cc.Slider)
        var sliderSound = cc.find('Canvas/set_layer/set_node/slider_sound').getComponent(cc.Slider)
        _musicMask.width = 500 * wsft.audioFuncs.musicVolume;
        _soundMask.width = 500* wsft.audioFuncs.soundVolume;
        sliderMusic.progress = wsft.audioFuncs.musicVolume;
        sliderSound.progress = wsft.audioFuncs.soundVolume;
    },
    //_checkBox
    checkBoxCallback:function(toggle, name){
        console.log(toggle)
        if(name == "toggle"){
            if(toggle.isChecked){ //cc.Color(255, 255, 73)
                wsft.uiFuncs.showTip('暂时未开通GPS功能！',30,2,(new cc.Color(0, 0, 0)))
            }else{
                wsft.uiFuncs.showTip('暂时未开通GPS功能',30,2,(new cc.Color(0, 0, 0)))
            }
        }
    },
    initcheckBox:function(){
        var self = this 
        var checkBoxhander = function(_sliderPath,_name){
            var slider = cc.find(_sliderPath)
            wsft.event.addCheckBoxEvent(slider,self.node,'setLayer','checkBoxCallback',_name)
        }
        checkBoxhander("Canvas/set_layer/distance_toggle","toggle")
    },
    //btn
    allBtnCallback:function(event,name){
        cc.log(name)
        if(name === "close"){
            this.node.active = false;
        }
        else if(name == 'change'){
            cc.director.loadScene('LoginScene')
        }
        else{
            
        }

    },
    initAllBtn:function(){
        var self = this
        var btnHader = function(btnPath,name){
            var btn = cc.find(btnPath);
            wsft.event.addBtnEvent(btn,self.node,'setLayer','allBtnCallback',name);
        }
        btnHader('Canvas/set_layer/close_btn','close'); //close
        btnHader('Canvas/set_layer/change_account_btn','change'); //更换账号
    },
    //slider
    sliderCallback:function(slider,name){
        // console.log(slider)
        console.log(name)
        if (name == "music") {
            // this._musicMask.width = this._musicWidth * slider.progress;
            wsft.audioFuncs.setMusicVolume(slider.progress)
        } else if(name == "sound") {
            wsft.audioFuncs.setSoundVolume(slider.progress)
            // this._soundMask.width = this._soundWidth * slider.progress;
        }
        this.refreshVolume()
    },
    initSlider:function(){
        var self = this 
        var sliderhander = function(_sliderPath,_name){
            var slider = cc.find(_sliderPath)
            wsft.event.addSliderEvent(slider,self.node,'setLayer','sliderCallback',_name)
        }
        sliderhander("Canvas/set_layer/set_node/slider_music","music")
        sliderhander("Canvas/set_layer/set_node/slider_sound","sound")
    },
    onDisable(){
        
    },

    // update (dt) {},
});
